
.prizeRecord {
  min-height: 100vh;
  background: #f6f6f6;
  .contentBox {
    display: flex;
    justify-content: center;
    .content {
      width: 100%;
      display: flex;
      justify-content: center;
      margin: 8px 0 0;
      .information {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
        .container {
          width: 100%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          background: #fff;
          border-bottom: 1px solid #f7f7f7;
          padding: 7px 0;
          .container-left {
            display: flex;
            flex-direction: column;
            margin: 0 0 0 20px;
            .caption {
              display: flex;
              .title {
                width: 3.13rem;
                font-size: 14px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #323332;
                line-height: 20px;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
              }
            }
            .info {
              display: flex;
              align-items: center;
              .award {
                width: 0.45rem;
                height: 0.18rem;
                background: linear-gradient(270deg, #ff5736 0%, #ff2a00 100%);
                border-radius: 37px;
                display: flex;
                justify-content: center;
                align-items: center;
                .message {
                  font-size: 12px;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #ffffff;
                  line-height: 12px;
                }
              }
              .time {
                font-size: 14px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #999999;
                line-height: 14px;
                margin: 0 0 0 8px;
              }
            }
          }
          .container-right {
            display: flex;
            margin: 0 20px 0 0;
            img {
              width: 8px;
              height: 14px;
            }
          }
        }
      }
      .information::-webkit-scrollbar {
        display: none;
      }
    }
  }
}
